import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';

const useStyles = makeStyles(() => ({
    modal: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center"
    },
    modalContents: {
        width: "98vw",
        objectFit: "contain",
        maxHeight: "98vh",
        backgroundColor: "white"
    },
    centerImage: {
        textAlign: "center",
        width: "100%",
    },
    imageContents: {
        maxWidth: "100%",
        maxHeight: "100%",
        objectFit: "contain",
        border: "1px solid #000000"
    }
}));
  
export default ({src, alt, fit}) => {
    const [open, setOpen] = React.useState(false);
    const openModal = () => {
        setOpen(true);
    };
    const closeModal = () => {
        setOpen(false);
    };
    const classes = useStyles();
    return (
        <>
            <div className={`${classes.centerImage} ${fit ? "fit" : ""}`} onClick={openModal} role="presentation">
                <img src={src} alt={alt ? alt : ""} className={classes.imageContents} />
            </div>
            <Modal open={open} onClose={closeModal} className={classes.modal} onClick={closeModal}>
                <img src={src} alt={alt ? alt : ""} className={classes.modalContents} />
            </Modal>
        </>
    );
};
